
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import recommend from '../../components/goods/recommend'
export default {
name: 'my_wash_card_list',
components: {
Tab,
TabItem,
Toast,
Scroller,
Divider,
recommend
},
data() {
return {
current_tab_index: 0,
instruc: {
title:''
},
list: {
'0_div': []
},
givedList: [],
pulldown_config: {
content: '下拉刷新',
height: 60,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '放开开始刷新',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup_config: {
content: '上拉加载更多',
pullUpHeight: 80,
height: 40,
autoRefresh: false,
downContent: '释放开始加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
},
recommend_list:[{id:'',name:'闲时卡',price:'10.00'},{id:'',name:'早鸟卡',price:'10.00'}]

}
},
computed: {

},
activated: function() {
var self = this;
self.resetData();
self.instrucFunc();
self.fetchData(0, '', self.current_tab_index);
self.fetchGivenData(0, '', self.current_tab_index);
},
methods: {
onItemClick(index) {
var self = this;
self.current_tab_index = index;
},
pulldown: function() {
var self = this;
var out_trade_no = (self.list[self.current_tab_index + '_div'][0] == undefined) ? '' : self.list[self.current_tab_index + '_div'][0]['out_trade_no'];
self.fetchData(1, out_trade_no, self.current_tab_index);
},
pullup: function() {
var self = this;
var out_trade_no = (self.list[self.current_tab_index + '_div'][(self.list[self.current_tab_index + '_div'].length - 1)] == undefined) ? '' : self.list[self.current_tab_index + '_div'][(self.list[self.current_tab_index + '_div'].length - 1)]['out_trade_no'];
self.fetchData(2, out_trade_no, self.current_tab_index);
},
instrucFunc: function(){
var self = this;
self.xarpost('Mallvcard/getCustomerCardInfo', {}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

self.instruc.title = data.msg;
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
fetchData: function(request_type, out_trade_no, tab_index) {

var self = this;
self.xarpost('Mallvcard/get_vcard_list', {
'type': request_type,
'id': out_trade_no,
'card_type': tab_index
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

if((request_type == 0 || request_type == 1)) {

} else {

}
var newList = (d.data == undefined) ? [] : d.data;
self.setListVal(newList, request_type, tab_index);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
fetchGivenData: function(request_type, id, tab_index) {

var self = this;
self.xarpost('Mallvcard/getGivenList', {
'type': request_type,
'id': id,
'card_type': tab_index
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

if((request_type == 0 || request_type == 1)) {

} else {

}
var newList = (d.data == undefined) ? [] : d.data;
self.setGivenListVal(newList, request_type, tab_index);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
showDetail: function(id){
this.$router.push({
path: '/wash_card_detail/'+id,
params: {
id: 0
}
});
return;
},
setListVal: function(newList, request_type, tab_index) {
var self = this;
if((request_type == 0 || request_type == 1)) {

if(newList.length <= 0) {

} else {
for(var i = newList.length - 1; i >= 0; i--) {
self.list[tab_index + '_div'].unshift(newList[i]);
}
}
}
if(request_type == 2) {

if(newList.length <= 0) {

} else {
for(var i = 0; i < newList.length; i++) {
self.list[tab_index + '_div'].push(newList[i]);
}
}
}
},
setGivenListVal(newList, request_type, tab_index){
var self = this;
if((request_type == 0 || request_type == 1)) {

if(newList.length <= 0) {

} else {
for(var i = newList.length - 1; i >= 0; i--) {
self.givedList.unshift(newList[i]);
}
}
}
if(request_type == 2) {

if(newList.length <= 0) {

} else {
for(var i = 0; i < newList.length; i++) {
self.givedList.push(newList[i]);
}
}
}
},
resetData: function() {
var self = this;
self.current_tab_index = 0;
self.list = {
'0_div': []
};
},
showOrderDetail: function(){
this.$router.push({
path: "/order_detail/Snorder120171223151804A12345",
});
return;
}
}
}



